<template>
    <div class="main-page">
        <List ref="list" :apis="apis" :render="render" :fieldToPage="fieldToPage" FormCode="oa_stockBill" :aePageUrl="`/modular/oa/StockBillAE?BillType=${$route.query.BillType}`" :searchInfo="{BillType:$route.query.BillType}">
            <el-button slot="contentRight" type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        </List>
    </div>
</template>

<script>
export default {
    name: "StockBillList",
    props: {},
