        List: () => import("@/universalPage/list/List.vue")
    },
    data() {
        return {
            apis: {
                edit: this._api.OSMS_StockBillEdit,
                create: this._api.OSMS_StockBillCreate,
                list: this._api.OSMS_StockBillManage,
                delete: this._api.OSMS_StockBillDelete,
                audit: this._api.OSMS_StockBillAudit,
                details: this._api.OSMS_StockBillDetails
            },
