                    <el-table-column prop="BillType" label="类型" width="100" align="center"></el-table-column>
                    <el-table-column prop="OperatorName" label="操作人" width="100" align="center"></el-table-column>
                    <el-table-column prop="FDate" label="日期" width="100" align="center" formatter={(row) => {
                        return (
                            <span>{this.$time(row.FDate).format("YYYY-MM-DD")}</span>
                        )
                    }}></el-table-column>
                    <el-table-column prop="CreateName" label="创建人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>
